@import url(https://fonts.googleapis.com/css?family=Seaweed+Script&display=swap);
@import url(https://fonts.googleapis.com/css?family=Prompt:400,400i,700&display=swap);
.header{z-index:1;font-family:'Seaweed Script', cursive;display:flex;justify-content:center;height:70px;background:#FFFFFF;box-shadow:0px 4px 4px rgba(0,0,0,0.25);position:fixed;width:100%}.header__h1{font-style:normal;font-weight:normal;font-size:30px;line-height:41px;letter-spacing:0.51em;color:#FF8A00;margin-top:13px 0}.header__logo{height:50px;width:50px;margin-top:8px}

.main-photo{background:url("/img/photo1.jpg");background-size:153%;background-position:-58px -20px;grid-area:foto}

.main-info{padding-left:50px;grid-area:info}.main-info__name{font-family:"Russell";letter-spacing:1px;margin:44px 0 5px}.main-info__name,.main-info .main-info__status{text-align:left;text-transform:uppercase}.main-info__status{background:#39393a;height:17px;padding:7px;font-size:14px;color:#d2c166;background-position:-36px -88px;background-size:1118px;background-repeat:no-repeat}.main-info__address{font-size:10px;color:#383838;padding:10px 0 0 2px;text-align:left;font-weight:700;font-style:italic}

.profile-title{height:104px;margin-top:-108px;padding-top:14px;padding-right:20px;font-size:14px;line-height:22px;color:white;text-align:right;letter-spacing:1.5px;text-transform:uppercase;background-color:#a98500b8;grid-area:title}.profile-title__square{width:47px;height:10px}

.profile-description{height:108px;box-sizing:border-box;margin-top:-108px;text-align:left;color:white;font-size:11px;line-height:14px;letter-spacing:1.1px;padding:16px 30px 13px 50px;background:#b39933;grid-area:description}

.contacts-wrap{background:#262730;color:white;padding:9px 21px 0 0;position:relative;grid-area:contacts}.contacts-wrap h3{text-transform:uppercase;font-size:15px;color:#d8ba43;border-bottom:2px solid #d8ba43;padding-bottom:4px;font-family:"roboto";text-align:right}.contacts-wrap__accent-yellow{width:27px;height:4px;background-color:#d8ba43;position:absolute;right:21px;top:28px}.contacts-wrap__contacts{padding-left:16px}.contacts-wrap__contactme{position:relative;margin-top:4px;list-style-type:none;text-align:right;display:flex;flex-wrap:nowrap;justify-content:flex-end}.contacts-wrap__contactme img{height:15px;position:absolute;left:-3px;top:3.4px;transition:transform 0.3s}.contacts-wrap__contactme:hover img{transform:scaleX(-1)}.contacts-wrap__home-link,.contacts-wrap .contacts-wrap__call-link{color:#fff}.contacts-wrap__call-link{display:flex;flex-wrap:nowrap;justify-content:flex-end}.contacts-wrap__contact-title{position:absolute;top:12px;right:42px;font-size:9px;letter-spacing:.7px}.contacts-wrap__contact-styles{margin-bottom:-8px;display:block;font-size:11px;text-transform:uppercase}.contacts-wrap__icon-border{border:1px solid;border-radius:50px;width:24px;height:24px;margin-top:13px;display:inline-block;cursor:pointer;transition:border 0.3s}.contacts-wrap__icon-border:hover{border:1px solid #d8ba43}.contacts-wrap__icon-border1 img{position:relative;left:-5px;top:4px}.contacts-wrap__icon-border2 img{position:relative;left:-.6px;top:2px;width:22px;height:21px}.contacts-wrap__icon-border3 img{position:relative;left:-5px;top:4px}.contacts-wrap__icon-border4 img{position:relative;left:-4px;top:4px}.contacts-wrap__telegram-link{text-decoration:none;color:#fff}

.exp{position:relative;grid-area:exp}.exp__pe-title{margin:23px 0 14px 52px;font-size:13px;font-weight:bold;text-align:left;padding:4px 0 5px;color:#41414a;letter-spacing:0.8px;text-transform:uppercase;border-bottom:2px solid #80808091}.exp__pe-description{font-size:11px;height:17px;letter-spacing:0.7px;margin:0 0 0 77px;padding-bottom:6px;text-align:left}.exp__pe-title-1{padding-bottom:10px;margin-top:15px}.exp__accent-gray{width:122px;height:4px;background-color:#828282;position:absolute;left:52px;top:29px}.exp__accent-gray2{top:111px}.exp__skillset{font-size:12px;text-align:left;margin-left:174px;border-left:2px solid #cecece}.exp__item-wrap{line-height:23px;position:relative;left:-98px}.exp__item{font-weight:bold;font-size:12px;position:absolute;width:98px;border-bottom:2px solid #cecece}.exp__hr{width:213px;background-color:#cecece;border:none;color:#cecece;background-color:#cecece;height:2px;position:absolute;left:97px;bottom:-8px}.exp__skill{display:flex;text-align:left;padding:0 20px 0 100px;font-size:11px;align-items:baseline}

.education{background:#363740;color:white;grid-area:education}.education__education-title{font-size:12px;letter-spacing:1.4px;background:#d8ba43;color:#3d4047;font-weight:bold;margin:0;padding:11px 22px;text-transform:uppercase;text-align:right}.education__vuz{display:block;padding:12px 20px 0;font-size:9px;text-align:right;letter-spacing:0.67px}.education__qualify{margin-bottom:-4px;letter-spacing:0.5px;display:block;font-size:12px;text-transform:uppercase}

.cv{position:relative;display:grid;grid-template:298px 0 260px 260px / 200px 1fr;grid-template-areas:"foto info " "title description " "contacts exp" "education exp";width:690px;height:818px;background-color:white;box-shadow:10px 10px 15px -8px rgba(0,0,0,0.29);border-radius:1px;margin:20px 0 0 163px}

.project{margin:-40px 0;background-size:100%;position:relative;perspective:500px;transform:rotate(-12deg);top:310px;left:83px;display:block}.project__container{height:90px;width:175px;box-shadow:1px -1px 5px 0px rgba(0,0,0,0.25);transition:transform 0.2s}.project__container:hover{transform:translate(-2px, -38px)}.project__name-Uber_eats{background:url("/img/visitki/uber.jpg");transform:rotate3d(2, 1, 14, -10deg)}.project__name-Uber_eats .project__name{color:white;font-size:8px;position:absolute;top:37px;right:8px;width:73px}.project__name-portfolio_layout_miami{background:url("/img/visitki/miami.jpg");transform:rotate3d(85, 0, -1, 23deg)}.project__name-portfolio_layout_miami .project__name{color:white;font-size:10px;position:absolute;top:64px;left:10px}.project__name-js_test{background:url("/img/visitki/test.jpg");transform:rotate3d(3, 0, -1, 23deg)}.project__name-js_test .project__name{color:#2f2f2f;font-size:8px;position:absolute;top:77px;left:0;width:175px}.project__name-portfolio_layout_dia{background:url("/img/visitki/dia.jpg");transform:rotate3d(3, 0, -1, 23deg)}.project__name-portfolio_layout_dia .project__name{color:white;font-size:8px;position:absolute;top:77px;left:0;width:175px}

.App{text-align:center;font-family:'Prompt', sans-serif;background:url("/img/gazon.jpg");padding-top:1vh;height:100vh;box-sizing:border-box}.flex-wrap{background:url("/img/fon.jpg");display:flex;position:relative;max-width:1280px;margin:auto;box-shadow:0px 0px 18px 2px rgba(0,0,0,0.75);border-radius:10px;box-sizing:border-box;padding-bottom:30px}.pen{background:url("/img/pen.png");width:32px;height:512px;background-size:66%;background-repeat:no-repeat;position:absolute;right:44px;top:14px}.cup{background:url("/img/cup.png");width:331px;height:296px;position:absolute;right:-84px;top:95px;background-size:71%;background-repeat:no-repeat}.iphone{background:url("/img/i-keys.png");width:331px;height:336px;position:absolute;right:-136px;top:394px;transform:rotate(-25deg);background-size:56%;background-repeat:no-repeat}.projects__project:hover{z-index:1}.projects__project-main{background:url("/img/projects-main.jpg");box-sizing:border-box;width:170px;height:93px;position:absolute;background-repeat:no-repeat;background-position:center;background-size:100%;padding-top:57px;text-transform:uppercase;font-size:11px;color:#442e27;left:59px;top:394px;box-shadow:1px -1px 1px -1px #000}.projects__project1{background-color:#fff;box-sizing:border-box;width:170px;height:99px;position:absolute;padding-top:10px;text-transform:uppercase;font-size:11px;color:#442e27;left:59px;top:364px;box-shadow:4px 3px 2px -1px rgba(0,0,0,0.1)}.projects__lepestok{background:url("/img/lepestok.png");width:331px;height:336px;position:absolute;right:136px;top:294px}@media screen and (max-width: 1366px){.cv{margin:20px 0 0 84px;transform:scale(0.95)}.projects{display:none}}@media screen and (max-width: 1024px){.cv{width:700px;margin:auto}}@media screen and (max-width: 1200px){.cup,.iphone,.pen{display:none}.ihpone{background:url("/img/i-keys.png");width:331px;height:272px;position:absolute;right:48px;top:394px;transform:rotate(-19deg);background-size:100%;background-repeat:no-repeat;width:156px}.cup{background:url("/img/cup.png");width:331px;height:177px;position:absolute;right:74px;top:95px;background-size:103%;background-repeat:no-repeat;width:200px}.project{left:52px}}@font-face{font-family:'Neverwinter';src:url("/fonts/Neverwinter.ttf") format("truetype");font-weight:normal;font-style:normal}@font-face{font-family:'Russell';src:url("/fonts/Russell.otf") format("truetype");font-weight:normal;font-style:normal}

